import React, { ComponentPropsWithoutRef } from 'react'
import { Link } from 'gatsby'
import { FontAwesomeIconProps } from '@fortawesome/react-fontawesome'
import { classnames } from 'tailwindcss-classnames'

// TODO: base, primary etc.
export type VariantType = 'primary' | 'secondary' | 'danger'
export type ButtonProps = {
  to?: string
  variant?: VariantType
  icon?: FontAwesomeIconProps
} & ComponentPropsWithoutRef<'button'>

// TODO: fix button classes
const button = classnames(
  'px-4',
  'py-1',
  'm-2',
  'inline-block',
  'text-lg',
  'text-gray-800',
  'font-semibold',
  'rounded-lg',
  'border-2',
  'border-gray-200',
  'hover:text-gray-200',
  'hover:bg-gray-800',
  'hover:border-gray-800',
)
const active = classnames('text-blue-200', 'bg-gray-800', 'border-gray-800')

const isActive: any = (args: any) => {
  return args.isCurrent ? { className: classnames(button, active) } : {}
}

export const Button: React.FC<ButtonProps> = ({
  variant = 'primary',
  children,
  to,
  icon,
  ...rest
}) => {
  return to ? (
    <Link to={to} className={button} getProps={isActive}>
      {children}
    </Link>
  ) : (
    <button className={button} {...rest}>
      {/* fix here, icon in button */}
      {/* {icon && <FontAwesomeIcon {...icon} />} */}
      {children}
    </button>
  )
}

export default Button
